<template>
  <div class="multiple-select-container">
    <el-select
      v-if="setting.key === ':args'"
      :value="data[setting.key]"
      :data-search="setting.key || setting.group"
      multiple
      filterable
      allow-create
      class="input"
      @change="updateSetting($event, settingGroup.group, settingGroup.key, setting.key, setting.type)">
      <el-option value="strip"/>
      <el-option value="auto-orient"/>
      <!-- eslint-disable -->
      <el-option value='{"implode", "1"}'/>
      <!-- eslint-enable -->
    </el-select>
  </div>
</template>

<script>
export default {
  name: 'SpecificMultipleSelect',
  props: {
    data: {
      type: [Object, Array],
      default: function() {
        return {}
      }
    },
    setting: {
      type: Object,
      default: function() {
        return {}
      }
    },
    settingGroup: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  methods: {
    updateSetting(value, group, key, input, type) {
      this.$store.dispatch('UpdateSettings', { group, key, input, value, type })
      this.$store.dispatch('UpdateState', { group, key, input, value })
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss'>
@import '../../../styles/settings';
@include settings
</style>
